<template><div class="markdown-body"><h1>日期选择器</h1>
<p><code v-pre="">mu-date-picker</code> 用于选择日期。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex justify-content=\&quot;between\&quot; align-items=\&quot;end\&quot; wrap=\&quot;wrap\&quot;&gt;\n    &lt;mu-paper :z-depth=\&quot;1\&quot; class=\&quot;demo-date-picker\&quot;&gt;\n      &lt;mu-date-picker :date.sync=\&quot;date\&quot;&gt;&lt;/mu-date-picker&gt;\n    &lt;/mu-paper&gt;\n    &lt;mu-paper :z-depth=\&quot;1\&quot; class=\&quot;demo-date-picker\&quot;&gt;\n      &lt;mu-date-picker landscape :date.sync=\&quot;date\&quot;&gt;&lt;/mu-date-picker&gt;\n    &lt;/mu-paper&gt;\n  &lt;/mu-flex&gt;\n&lt;/mu-container&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      date: undefined\n    };\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.demo-date-picker {\n  margin: 8px;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex justify-content="between" align-items="end" wrap="wrap">
    <mu-paper :z-depth="1" class="demo-date-picker">
      <mu-date-picker :date.sync="date"></mu-date-picker>
    </mu-paper>
    <mu-paper :z-depth="1" class="demo-date-picker">
      <mu-date-picker landscape="" :date.sync="date"></mu-date-picker>
    </mu-paper>
  </mu-flex>
</mu-container>


</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
