      <span class="hljs-tag">&lt;<span class="hljs-name">mu-date-picker</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"year"</span> <span class="hljs-attr">:date.sync</span>=<span class="hljs-string">"date2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-date-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-paper</span>&gt;</span>
