<h2 id="bu-tong-yu-yan-huan-jing"><a class="header-anchor" href="#bu-tong-yu-yan-huan-jing" aria-hidden="true">¶</a> 不同语言环境</h2>
<p>通过自定义 <code v-pre="">date-time-format</code> 来格式化不同语言的显示</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex wrap=\&quot;wrap\&quot;&gt;\n    &lt;mu-paper :z-depth=\&quot;1\&quot; class=\&quot;demo-date-picker\&quot;&gt;\n      &lt;mu-date-picker :date-time-format=\&quot;enDateFormat\&quot; :date.sync=\&quot;date4\&quot;&gt;&lt;/mu-date-picker&gt;\n    &lt;/mu-paper&gt;\n  &lt;/mu-flex&gt;\n&lt;/mu-container&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nconst dayAbbreviation = ['S', 'M', 'T', 'W', 'T', 'F', 'S'];\nconst dayList = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];\nconst monthList = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep',\n  'Oct', 'Nov', 'Dec'];\nconst monthLongList = ['January', 'February', 'March', 'April', 'May', 'June',\n  'July', 'August', 'September', 'October', 'November', 'December'];\n\nconst enDateFormat = {\n  formatDisplay (date) {\n    return `${dayList[date.getDay()]}, ${monthList[date.getMonth()]} ${date.getDate()}`;\n  },\n  formatMonth (date) {\n    return `${monthLongList[date.getMonth()]} ${date.getFullYear()}`;\n  },\n  getWeekDayArray (firstDayOfWeek) {\n    let beforeArray = [];\n    let afterArray = [];\n    for (let i = 0; i &lt; dayAbbreviation.length; i++) {\n      if (i &lt; firstDayOfWeek) {\n        afterArray.push(dayAbbreviation[i]);\n      } else {\n        beforeArray.push(dayAbbreviation[i]);\n      }\n    }\n    return beforeArray.concat(afterArray);\n  },\n  getMonthList () {\n    return monthList;\n  }\n};\n\nexport default {\n  data () {\n    return {\n      date4: undefined,\n      enDateFormat\n    }\n  }\n};\n&quot;,&quot;style&quot;:&quot;\n.demo-date-picker {\n  margin: 8px;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex wrap="wrap">
    <mu-paper :z-depth="1" class="demo-date-picker">
      <mu-date-picker :date-time-format="enDateFormat" :date.sync="date4"></mu-date-picker>
    </mu-paper>
  </mu-flex>
</mu-container>


</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">wrap</span>=<span class="hljs-string">"wrap"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-paper</span> <span class="hljs-attr">:z-depth</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-date-picker"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-date-picker</span> <span class="hljs-attr">:date-time-format</span>=<span class="hljs-string">"enDateFormat"</span> <span class="hljs-attr">:date.sync</span>=<span class="hljs-string">"date4"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-date-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-paper</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">const</span> dayAbbreviation = [<span class="hljs-string">'S'</span>, <span class="hljs-string">'M'</span>, <span class="hljs-string">'T'</span>, <span class="hljs-string">'W'</span>, <span class="hljs-string">'T'</span>, <span class="hljs-string">'F'</span>, <span class="hljs-string">'S'</span>];
<span class="hljs-keyword">const</span> dayList = [<span class="hljs-string">'Sun'</span>, <span class="hljs-string">'Mon'</span>, <span class="hljs-string">'Tue'</span>, <span class="hljs-string">'Wed'</span>, <span class="hljs-string">'Thu'</span>, <span class="hljs-string">'Fri'</span>, <span class="hljs-string">'Sat'</span>];
<span class="hljs-keyword">const</span> monthList = [<span class="hljs-string">'Jan'</span>, <span class="hljs-string">'Feb'</span>, <span class="hljs-string">'Mar'</span>, <span class="hljs-string">'Apr'</span>, <span class="hljs-string">'May'</span>, <span class="hljs-string">'Jun'</span>, <span class="hljs-string">'Jul'</span>, <span class="hljs-string">'Aug'</span>, <span class="hljs-string">'Sep'</span>,
  <span class="hljs-string">'Oct'</span>, <span class="hljs-string">'Nov'</span>, <span class="hljs-string">'Dec'</span>];
<span class="hljs-keyword">const</span> monthLongList = [<span class="hljs-string">'January'</span>, <span class="hljs-string">'February'</span>, <span class="hljs-string">'March'</span>, <span class="hljs-string">'April'</span>, <span class="hljs-string">'May'</span>, <span class="hljs-string">'June'</span>,
  <span class="hljs-string">'July'</span>, <span class="hljs-string">'August'</span>, <span class="hljs-string">'September'</span>, <span class="hljs-string">'October'</span>, <span class="hljs-string">'November'</span>, <span class="hljs-string">'December'</span>];

<span class="hljs-keyword">const</span> enDateFormat = {
  formatDisplay (date) {
    <span class="hljs-keyword">return</span> <span class="hljs-string">`<span class="hljs-subst">${dayList[date.getDay()]}</span>, <span class="hljs-subst">${monthList[date.getMonth()]}</span> <span class="hljs-subst">${date.getDate()}</span>`</span>;
  },
  formatMonth (date) {
    <span class="hljs-keyword">return</span> <span class="hljs-string">`<span class="hljs-subst">${monthLongList[date.getMonth()]}</span> <span class="hljs-subst">${date.getFullYear()}</span>`</span>;
  },
  getWeekDayArray (firstDayOfWeek) {
    <span class="hljs-keyword">let</span> beforeArray = [];
    <span class="hljs-keyword">let</span> afterArray = [];
    <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = <span class="hljs-number">0</span>; i &lt; dayAbbreviation.length; i++) {
      <span class="hljs-keyword">if</span> (i &lt; firstDayOfWeek) {
        afterArray.push(dayAbbreviation[i]);
      } <span class="hljs-keyword">else</span> {
        beforeArray.push(dayAbbreviation[i]);
      }
    }
    <span class="hljs-keyword">return</span> beforeArray.concat(afterArray);
  },
  getMonthList () {
    <span class="hljs-keyword">return</span> monthList;
  }
};

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">date4</span>: <span class="hljs-literal">undefined</span>,
      enDateFormat
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.demo-date-picker</span> {
  <span class="hljs-attribute">margin</span>: <span class="hljs-number">8px</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="date-picker-props"><a class="header-anchor" href="#date-picker-props" aria-hidden="true">¶</a> Date Picker Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>日期选择器颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>date</td>
<td>选择日期，支持 <code v-pre="">.sync</code> 修饰符</td>
<td>Date</td>
<td>—</td>
<td>new Date()</td>
</tr>
<tr>
<td>type</td>
<td>选择的类型</td>
<td>String</td>
<td>date/year/month</td>
<td>date</td>
</tr>
<tr>
<td>date-time-format</td>
<td>日期格式化对象，用于改变不同的语言环境显示</td>
<td>Object</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>first-day-of-week</td>
<td>哪一天作为一个星期的开始</td>
<td>Number</td>
<td>0-6</td>
<td>0</td>
</tr>
<tr>
<td>max-date</td>
<td>最大可选择日期</td>
<td>Date</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>min-date</td>
<td>最小可选择日期</td>
<td>Date</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>landscape</td>
<td>是否横屏显示</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>no-display</td>
<td>是否不存在日期显示部分</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>should-disable-date</td>
<td>判断日期是否不可用的函数</td>
<td>Function</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="date-picker-events"><a class="header-anchor" href="#date-picker-events" aria-hidden="true">¶</a> Date Picker Events</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>当选择日期改变时触发事件</td>
<td>(date)</td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style>
.demo-date-picker {
  margin: 8px;
}
</style>
<script>
const dayAbbreviation = ['S', 'M', 'T', 'W', 'T', 'F', 'S'];
const dayList = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
const monthList = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep',
  'Oct', 'Nov', 'Dec'];
const monthLongList = ['January', 'February', 'March', 'April', 'May', 'June',
  'July', 'August', 'September', 'October', 'November', 'December'];

const enDateFormat = {
  formatDisplay (date) {
    return `${dayList[date.getDay()]}, ${monthList[date.getMonth()]} ${date.getDate()}`;
  },
  formatMonth (date) {
    return `${monthLongList[date.getMonth()]} ${date.getFullYear()}`;
  },
  getWeekDayArray (firstDayOfWeek) {
    let beforeArray = [];
    let afterArray = [];
    for (let i = 0; i < dayAbbreviation.length; i++) {
      if (i < firstDayOfWeek) {
        afterArray.push(dayAbbreviation[i]);
      } else {
        beforeArray.push(dayAbbreviation[i]);
      }
    }
    return beforeArray.concat(afterArray);
  },
  getMonthList () {
    return monthList;
  }
};

export default {
  data () {
    return {
      date: undefined,
      date1: undefined,
      date2: undefined,
      date3: undefined,
      date4: undefined,
      enDateFormat
    };
  },
  methods: {
    allowedDates (date) {
      return date.getDate() % 2 === 0
    }
  }
}
</script>


